	export default {
		data() {
			return {
				ongoing_info:'',
				roles:'',
				ding:false
			}
		},
		onLoad(e) {
			this.roles = e.id
			this.orderIndex()
		},
		methods: {
			async orderIndex(uid){
				uni.showLoading({})
				const that = this
				var tokes = uni.getStorageSync("token")
				var uid = tokes.uid
				const res = await this.$myRequest({
					method:"POST",
					url:"/selOrder?roles=" + that.roles
				})
				uni.hideLoading()
				if(res.data.data == ""){
					that.ding = true
				}else{
					that.ding = false
					that.ongoing_info = res.data.data
				}
			},
			async daSao(oid){
				const oids = oid
				const that = this
				uni.showModal({
					title:"确定打扫房间吗?",
					success(res) {
						if(res.confirm){
							that.daIndex(oids)
							uni.hideLoading()
						}else if(res.cancel){
							uni.hideLoading()
						}
					}
				})
			},
			async daIndex(oid){
				const that = this
				uni.showLoading({})
				const res = await this.$myRequest({
					method:"POST",
					url:"/clearHouse?oid=" + oid
				})
				uni.hideLoading()
				that.code = res.data.code
				if(that.code == 1){
					uni.showToast({
						title:"该房间已打扫"
					})
					setTimeout(()=>{
						uni.switchTab({
							url:"../ourselves/ourselves"
						})
					},2000)
				}else{
					uni.showToast({
						title:"请联系客服"
					})
				}
			}
	}
}
		
	
